<template>
    <div class="points-mapping d-flex">
        <VRow dense class="flex-grow-1">
            <VCol cols="6">
                <CoordsPair
                        :x-label="ulabel"
                        :y-label="vlabel"
                        :x.sync="uval"
                        :y.sync="vval"
                        :disabled="disabled"
                />
            </VCol>

            <VCol cols="6">
                <CoordsPair
                        :x-label="xlabel"
                        :y-label="ylabel"
                        :x.sync="xval"
                        :y.sync="yval"
                        :disabled="disabled"
                />
            </VCol>
        </VRow>

        <VMenu left v-model="showMenu" v-if="activeOffset === null">
            <template v-slot:activator="{on}">
                <VBtn icon class="mt-2 ml-2" v-on="on" :disabled="disabled">
                    <VIcon>mdi-dots-vertical</VIcon>
                </VBtn>
            </template>

            <VList>
                <VMenu :offset-x="true" left>
                    <template v-slot:activator="{on}">
                        <VListItem v-on="on">
                            <VListItemTitle>
                                <VIcon>mdi-menu-left</VIcon>
                                Pick Source Point
                            </VListItemTitle>
                        </VListItem>
                    </template>

                    <VList>
                        <VListItem @click="pickPoint(0)">
                            <VListItemIcon>
                                <VIcon>mdi-target</VIcon>
                            </VListItemIcon>
                            <VListItemTitle>
                                From Image
                            </VListItemTitle>
                        </VListItem>

                        <VListItem @click="pickApex('tl', 0)">
                            <VListItemIcon>
                                <VIcon>mdi-arrow-top-left</VIcon>
                            </VListItemIcon>
                            <VListItemTitle>
                                Image Top Left
                            </VListItemTitle>
                        </VListItem>

                        <VListItem @click="pickApex('tr', 0)">
                            <VListItemIcon>
                                <VIcon>mdi-arrow-top-right</VIcon>
                            </VListItemIcon>
                            <VListItemTitle>
                                Image Top Right
                            </VListItemTitle>
                        </VListItem>

                        <VListItem @click="pickApex('bl', 0)">
                            <VListItemIcon>
                                <VIcon>mdi-arrow-bottom-left</VIcon>
                            </VListItemIcon>
                            <VListItemTitle>
                                Image Bottom Left
                            </VListItemTitle>
                        </VListItem>

                        <VListItem @click="pickApex('br', 0)">
                            <VListItemIcon>
                                <VIcon>mdi-arrow-bottom-right</VIcon>
                            </VListItemIcon>
                            <VListItemTitle>
                                Image Bottom Right
                            </VListItemTitle>
                        </VListItem>
                    </VList>
                </VMenu>

                <VListItem @click="pickPoint(2)">
                    <VListItemIcon>
                        <VIcon>mdi-target-variant</VIcon>
                    </VListItemIcon>
                    <VListItemTitle>
                        Pick Destination Point
                    </VListItemTitle>
                </VListItem>

                <VListItem :disabled="!enableDeletion" @click="$emit('remove-control-point')">
                    <VListItemTitle>
                        Remove Mapping
                    </VListItemTitle>
                </VListItem>
            </VList>
        </VMenu>

        <VBtn v-else icon class="mt-2 ml-2" @click="$emit('pick-point-cancel')">
            <VIcon>mdi-cancel</VIcon>
        </VBtn>
    </div>
</template>

<script>
  import CoordsPair from './coords-pair';

  function createComputed(prop) {
    return {
      get() {
        return this[prop];
      },

      set(value) {
        this.$emit(`update:${prop}`, value);
      }
    };
  }

  export default {
    name: 'PointsMapping',

    components: {
      CoordsPair
    },

    props: {
      u: {
        type: Number,
        default: 0
      },

      v: {
        type: Number,
        default: 0
      },

      x: {
        type: Number,
        default: 0
      },

      y: {
        type: Number,
        default: 0
      },

      index: Number,

      disabled: Boolean,

      enableDeletion: {
        type: Boolean,
        default: true
      },

      activeOffset: {
        type: [Number, null],
        default: null
      }
    },

    data() {
      return {
        showMenu: false
      };
    },

    computed: {
      uval: createComputed('u'),
      vval: createComputed('v'),
      xval: createComputed('x'),
      yval: createComputed('y'),

      labelIndex() {
        return this.index !== undefined ? this.index : '';
      },

      ulabel() {
        return `u${this.labelIndex}`;
      },

      vlabel() {
        return `v${this.labelIndex}`;
      },

      xlabel() {
        return `x${this.labelIndex}`;
      },

      ylabel() {
        return `y${this.labelIndex}`;
      }
    },

    methods: {
      pickApex(location, offset) {
        this.$emit('pick-source-apex', {location, offset});
        this.showMenu = false;
      },

      pickPoint(offset) {
        this.$emit('pick-point', offset);
        this.showMenu = false;
      }
    }
  }
</script>