<template>
    <div>
        <h3 class="mb-3">Image Settings</h3>

        <ImageSettings
                :interpolation-method.sync="localImageOptions.interpolationMethod"
                :virtual-pixel-method.sync="localImageOptions.virtualPixelMethod"
                :background-color.sync="localImageOptions.backgroundColor"
                :viewport-offset.sync="localImageOptions.initialViewportOffset"
                :disabled="disabled"
                @change-image="$emit('change-image', $event)"

        />

        <VDivider/>

        <h3 class="mt-4 mb-3">Distortion Settings</h3>

        <DistortionSettings
                class="mb-3"
                :distortion.sync="localDistortion.name"
                :args.sync="localDistortion.args"
                :disabled="disabled"
                :source-image="sourceImage"
                :viewport-offset="imageOptions.initialViewportOffset"
                @pick-point="$emit('pick-point', $event)"
                @pick-point-cancel="$emit('pick-point-cancel', $event)"
                :active-offset="argsActiveOffset"
        />

        <VDivider/>

        <h3 class="mt-4 mb-3">Options</h3>

        <DistortionOptions
                :resample.sync="localDistortionOptions.resample"
                :filter-preset.sync="localDistortionOptions.filterPreset"
                :custom-filter-options.sync="localDistortionOptions.customFilterOptions"
                :matte-color.sync="localDistortionOptions.matteColor"
                :supersample.sync="localDistortionOptions.supersample"
                :best-fit.sync="localDistortionOptions.bestFit"
                :use-custom-viewport.sync="localDistortionOptions.useCustomViewport"
                :custom-viewport.sync="localDistortionOptions.customViewport"
                :disable-best-fit="bestFitViewportDisabled"
                :disable-source-viewport="sourceViewportDisabled"
                :disabled="disabled"
        />
        </VCardText>
    </div>
</template>

<script>
  import {createPropDataWatchers} from '../../../utils';
  import ImageSettings from './image-settings';
  import DistortionSettings from './distortion-settings/distortion-settings';
  import DistortionOptions from './distortion-options/distortion-options';

  export default {
    name: 'InputSettings',

    components: {
      ImageSettings,
      DistortionSettings,
      DistortionOptions
    },

    props: {
      imageOptions: Object,
      distortion: Object,
      distortionOptions: Object,
      sourceImage: Image,
      disabled: Boolean,
      argsActiveOffset: [Number, null]
    },

    data() {
      return {
        localImageOptions: {},
        localDistortion: {},
        localDistortionOptions: {},
      };
    },

    computed: {
      sourceViewportDisabled() {
        // Only best fit and custom viewport should be available for ARC distortion
        return this.localDistortion.name === 'ARC';
      },

      bestFitViewportDisabled() {
        // Made for future
        return false;
      }
    },

    watch: {
      ...createPropDataWatchers({
        imageOptions: 'localImageOptions',
        distortion: 'localDistortion',
        distortionOptions: 'localDistortionOptions'
      })
    },

    methods: {

    }
  }
</script>