<template>
    <VMenu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :close-on-click="false"
            :return-value.sync="color"
            transition="scale-transition"
            offset-y
            min-width="290px"
            :disabled="disabled"
    >
        <template v-slot:activator="{ on }">
            <VInput
                    :label="label"
            >
                <div class="transparent-backdrop" v-on="on">
                    <div class="color-sample" :class="{disabled: !!disabled}" :style="{backgroundColor: sheetColor}"></div>
                </div>
            </VInput>
        </template>

        <VCard>
            <VColorPicker
                    v-model="color"
                    :hide-mode-switch="true"
                    mode="rgba"
            />

            <VCardActions>
                <VBtn small @click="$refs.menu.save(color)" color="primary">
                    Ok
                </VBtn>

                <VBtn small @click="$refs.menu.save({r: 0, g: 0, b: 0, a: 0})" color="secondary">
                    Transparent
                </VBtn>

                <VBtn small @click="menu = false">
                    Cancel
                </VBtn>
            </VCardActions>
        </VCard>
    </VMenu>
</template>

<script>
  function alphaFractToQuantum(fract) {
    return Math.round(fract * 255);
  }

  function alphaQuantumToFract(quantum) {
    return quantum / 255;
  }

  function rgbaObjectToArray({r, g, b, a}) {
    return [
      r, g, b,
      alphaFractToQuantum(a)
    ];
  }

  function arrayToRgbaObject([r, g, b, a]) {
    a = alphaQuantumToFract(a);

    return {r, g, b, a};
  }

  function isSameColor(rgba1, rgba2) {
    return rgba1.r === rgba2.r  && rgba1.g === rgba2.g && rgba1.b === rgba2.b && rgba1.a === rgba2.a;
  }

  export default {
    name: 'ColorInput',

    props: {
      value: {
        type: Array,
        default() {
          return [255, 255, 255, 255];
        }
      },

      label: String,

      disabled: Boolean
    },

    data() {
      return {
        menu: false,
        color: arrayToRgbaObject(this.value)
      };
    },

    computed: {
      sheetColor() {
        return `rgba(${this.color.r}, ${this.color.g}, ${this.color.b}, ${this.color.a})`;
      }
    },

    watch: {
      color(newColor, oldColor) {
        if (!isSameColor(newColor, oldColor)) {
          this.$emit('input', rgbaObjectToArray(newColor));
        }
      }
    }
  }
</script>

<style scoped lang="scss">
    .transparent-backdrop {
        background-image: url(data:image/png;base64,);
        background-repeat: repeat;
        width: 100%;
        height: 20px;
        margin-top: 10px;
        border: #aaa 1px solid;

        .color-sample {
            width: 100%;
            height: 100%;
            cursor: pointer;

            &.disabled {
                cursor: default;
            }
        }
    }

    ::v-deep .v-label {
        transform-origin: top left;
        transform: translateY(-10px) scale(0.75);
        position: absolute!important;
        left: 0;
    }
</style>