<template>
    <div class="control-points">
        <div
                class="d-flex"
                v-for="([u, v, x, y], groupIndex) in this.controlPointsGroups"
                :key="groupIndex"
        >
            <PointsMapping
                    :index="groupIndex"
                    :u="u"
                    :v="v"
                    :x="x"
                    :y="y"
                    @update:u="setValue(groupIndex * 4, $event)"
                    @update:v="setValue(groupIndex * 4 + 1, $event)"
                    @update:x="setValue(groupIndex * 4 + 2, $event)"
                    @update:y="setValue(groupIndex * 4 + 3, $event)"
                    :disabled="disabled"
                    :active-offset="getActiveOffset(groupIndex)"
                    @pick-point="pickPoint($event, groupIndex)"
                    @pick-point-cancel="$emit('pick-point-cancel')"
                    @pick-source-apex="pickApex($event, groupIndex)"
                    @remove-control-point="removeControlPoint(groupIndex)"
                    :enable-deletion="enableDeletion"
            />
        </div>

        <VBtn
                :disabled="disabled"
                @click="addControlPoint()"
        >
            Add
        </VBtn>
    </div>
</template>

<script>
  import distortionArgsMixin from './distortion-args-mixin';
  import PointsMapping from './points-mapping';

  export default {
    name: 'ControlPoints',

    mixins: [distortionArgsMixin],

    components: {
      PointsMapping
    },

    props: {
      minControlPoints: {
        type: Number,
        default: 0
      },

      activeOffset: {
        type: [Number, null],
        default: null
      }
    },


    computed: {
      controlPointsGroups() {
        const groups    = [];

        for (let i = 0; i < this.localValue.length; i += 4) {
          groups.push(
            this.localValue.slice(i, i + 4)
          );
        }

        return groups;
      },

      controlPointsNumber() {
        return this.localValue.length / 4;
      },

      enableDeletion() {
        return this.controlPointsNumber > this.minControlPoints;
      }
    },

    methods: {
      addControlPoint(u = 0, v = 0, x = 0, y = 0) {
        this.localValue.splice(this.localValue.length, 0, u, v, x, y);
        this.emitInput();
      },

      removeControlPoint(groupIndex) {
        this.localValue.splice(groupIndex * 4, 4);
        this.emitInput();
      },

      pickApex({location, offset}, groupOffset) {
        offset += groupOffset * 4;
        this.$emit('pick-source-apex', {location, offset});
      },

      pickPoint(offset, groupOffset) {
        offset += groupOffset * 4;
        this.$emit('pick-point', offset);
      },

      getActiveOffset(groupIndex) {
        if (this.activeOffset === null) {
          return null;
        }

        const index = Math.floor(this.activeOffset / 4);

        return index === groupIndex ? this.activeOffset % 4 : null;
      }
    }
  }
</script>