<template>
    <div>
        <VCheckbox
                v-model="enabled"
                label="Supersampling"
                @change="emitUpdate"
                :disabled="disabled"
        />

        <VExpandTransition>
            <VTextField
                    v-if="enabled"
                    v-model.number="factor"
                    type="number"
                    label="Supersampling Factor"
                    dense
                    outlined
                    @input="emitUpdate"
                    :disabled="disabled"
            />
        </VExpandTransition>
    </div>
</template>

<script>
  export default {
    name: 'SupersamplingSettings',

    props: {
      supersample: [Boolean, Number],
      disabled: Boolean
    },

    data() {
      const enabled = this.supersample !== false;

      return {
        enabled,
        factor:  enabled ? this.supersample : 2
      };
    },

    methods: {
      emitUpdate() {
        if (this.enabled) {
          this.$emit('update:supersample', this.factor);
        } else {
          this.$emit('update:supersample', false);
        }
      }
    },

    watch: {
      supersample(newValue) {
        if (newValue !== false) {
          this.enabled = true;
          this.factor  = newValue;
        } else {
          this.enabled = false;
        }
      }
    }
  }
</script>

