<template>
    <div class="image-viewer" v-on="mouseTouchHandlers" :class="containerClass">
        <VPanzoom
                class="image-viewer__image"
                tag="div"
                :bounds="lockBounds"
                :smooth-scroll="false"
                :scale.sync="localScale"
                :position.sync="localPosition"
                :min-zoom="minScale"
                :max-zoom="maxScale"
                @panstart="onPanStart"
                @panend="onPanEnd"
        >
            <img :src="src" alt="" :style="imageStyle">
        </VPanzoom>
    </div>
</template>

<script>
  import VPanzoom from '../../../v-panzoom';

  function extractPageCoords(mouseTouchEvent) {
    if (mouseTouchEvent.changedTouches) {
      if (mouseTouchEvent.changedTouches.length === 1) {
        const {pageX, pageY} = mouseTouchEvent.changedTouches[0];
        return {pageX, pageY};
      }

      return null;
    }

    const {pageX, pageY} = mouseTouchEvent;

    return {pageX, pageY};
  }

  export default {
    name: 'ImageViewer',

    components: {
      VPanzoom
    },

    props: {
      src: String,

      position: {
        type: Object,
        default() {
          return {x: 0, y: 0};
        }
      },

      scale: {
        type: Number,
        default: 1
      },

      minScale: {
        type: Number,
        default: 0.1
      },

      maxScale: {
        type: Number,
        default: 4
      },

      lockBounds: Boolean,

      offset: {
        type: Object,
        default() {
          return {x: 0, y: 0}
        }
      }
    },

    data() {
      let mousedown, mouseup, touchstart, touchend, touchcancel;

      mousedown = mouseup = touchstart = touchend = touchcancel = this.mouseTouchHandler;

      return {
        isPanning: false,
        localScale: this.scale,
        localPosition: this.position,
        cursorStart: null,
        mouseTouchHandlers: {
          mousedown,
          mouseup,
          touchstart,
          touchend,
          touchcancel
        }
      };
    },

    computed: {
      imageStyle() {
        let left = 0;
        let top = 0;

        if (this.offset) {
          left = this.offset.x;
          top = this.offset.y;
        }

        left += 'px';
        top += 'px';

        return {left, top, position: 'relative'};
      },

      containerClass() {
        return {
          panning: this.isPanning
        };
      }
    },

    watch: {
      position({x, y}) {
        if (x !== this.localPosition.x || y !== this.localPosition.y) {
          this.localPosition = {x, y};
        }
      },

      scale(newScale) {
        if (newScale !== this.localScale) {
          this.localScale = newScale;
        }
      },

      localPosition({x, y}) {
        if (x !== this.position.x || y !== this.position.y) {
          this.$emit('update:position', {x, y});
        }
      },

      localScale(newScale) {
        if (newScale !== this.scale) {
          this.$emit('update:scale', newScale);
        }
      }
    },

    methods: {

      onPanStart() {
        this.isPanning = true;
      },

      onPanEnd() {
        this.isPanning = false;
      },

      mouseTouchHandler(event) {
        switch (event.type) {
          case 'mousedown':
          case 'touchstart':
            this.cursorStart = extractPageCoords(event);
            break;

          case 'mouseup':
          case 'touchend':
            this.mouseTouchEnd(event);
            break;

          case 'touchcancel':
          default:
            this.cursorStart = null;
            break;
        }
      },

      mouseTouchEnd(event) {
        if (this.cursorStart) {
          let coords = extractPageCoords(event);

          if (coords && coords.pageX === this.cursorStart.pageX && coords.pageY === this.cursorStart.pageY) {
            this.emitClick(coords);
          }

          this.cursorStart = null;
        }
      },

      emitClick({pageX, pageY}) {
        const {top, left} = this.$el.getBoundingClientRect();
        const containerX = pageX - left;
        const containerY = pageY - top;
        const scale = 1 / this.localScale;
        const x = (containerX - this.localPosition.x) * scale;
        const y = (containerY - this.localPosition.y) * scale;
        this.$emit('click', {x, y});
      }
    }
  }
</script>

<style scoped lang="scss">
    .image-viewer {
        position: relative;
        cursor: grab;

        &.panning {
            cursor: grabbing;
        }
    }

    .image-viewer__image {
        position: absolute;
    }
</style>