<template>
    <div>
        <h4>Resampling Options</h4>

        <VCheckbox
                v-model="proxyResample"
                label="EWA Resampling"
                dense
                :disabled="disabled || disableResampling"
        />

        <VExpandTransition>
            <FilterSettings
                    v-if="resample"
                    :filter-preset.sync="proxyFilterPreset"
                    :custom-filter-options.sync="proxyCustomFilterOptions"
                    :disabled="disabled"
            />
        </VExpandTransition>

        <SupersamplingSettings
                :supersample.sync="proxySupersample"
                :disabled="disabled"
        />

        <ViewportSettings
                :best-fit.sync="proxyBestFit"
                :use-custom-viewport.sync="proxyUseCustomViewport"
                :custom-viewport.sync="proxyCustomViewport"
                :best-fit-disabled="disableBestFit"
                :source-disabled="disableSourceViewport"
                :disabled="disabled"
        />

        <h4 class="mb-2">Other Settings</h4>

        <ColorInput
                label="Matte Color"
                v-model="proxyMatteColor"
                :disabled="disabled"
        />
    </div>
</template>

<script>
  import {massSimpleProxy} from '../../../../utils';
  import FilterSettings from './filter-settings';
  import ColorInput from '../color-input';
  import SupersamplingSettings from './supersampling-settings';
  import ViewportSettings from './viewport-settings';

  const props = {
    resample: Boolean,

    filterPreset: String,

    customFilterOptions: Object,

    supersample: [Boolean, Number],

    matteColor: Array,

    bestFit: Boolean,

    useCustomViewport: Boolean,

    customViewport: Object,

    disableResampling: Boolean,

    disableBestFit: Boolean,

    disableSourceViewport: Boolean,

    disabled: Boolean
  };

  export default {
    name: 'DistortionOptions',

    components: {
      FilterSettings,
      ColorInput,
      SupersamplingSettings,
      ViewportSettings
    },

    props,

    computed: {
      ...massSimpleProxy(Object.keys(props), 'proxy', ['disableResampling',
                                                       'disableBestFit',
                                                       'disableSourceViewport',
                                                       'disabled'])
    }
  }
</script>