<template>
    <div>
        <VSelect
                :items="distortionNames"
                v-model="proxyDistortion"
                label="Distortion"
                dense
                filled
                :disabled="disabled"
        />

        <h4>Distortion Arguments</h4>

        <h5 class="mt2">{{ distortionArgumentsName }}</h5>

        <ControlPoints
                v-if="isUsingContolPoints"
                v-model="localArgs"
                :min-control-points="minControlPoints"
                @pick-point="$emit('pick-point', $event)"
                @pick-point-cancel="$emit('pick-point-cancel')"
                @pick-source-apex="onPickSourceApex"
                :active-offset="activeOffset"
                :disabled="disabled"
        />

        <AffineProjectionArgs
                v-else-if="distortion === 'AFFINE_PROJECTION'"
                v-model="localArgs"
                :disabled="disabled"
        />

        <PerspectiveProjectionArgs
                v-else-if="distortion === 'PERSPECTIVE_PROJECTION'"
                v-model="localArgs"
                :disabled="disabled"
        />

        <ArcArgs
                v-else-if="distortion === 'ARC'"
                v-model="localArgs"
                :disabled="disabled"
        />
    </div>
</template>

<script>
  import {simpleProxy, arraysEqual} from '../../../../utils';
  import lens from '@alxcube/lens';
  import ControlPoints from '../distortion-arguments/control-points';
  import AffineProjectionArgs from '../distortion-arguments/affine-projection-args';
  import PerspectiveProjectionArgs from '../distortion-arguments/perspective-projection-args';
  import ArcArgs from '../distortion-arguments/arc-args';
  const {distorts} = lens;

  const props = {
    distortion: String,

    args: Array,

    disabled: Boolean,

    sourceImage: {
      type: Image,
      default() {
        return null;
      }
    },

    // Offset of control points pair being selected in distortion arguments array
    activeOffset: {
      type: [Number, null],
      default: null
    },

    viewportOffset: {
      type: Object,
      default() {
        return {x: 0, y: 0}
      }
    }
  };

  export default {
    name: 'DistortionSettings',

    components: {
      ControlPoints,
      AffineProjectionArgs,
      PerspectiveProjectionArgs,
      ArcArgs
    },

    props,

    data() {
      return {
        localArgs: []
      };
    },

    computed: {
      proxyDistortion: simpleProxy('distortion'),

      distortionNames() {
        return Object.keys(distorts);
      },

      isUsingContolPoints() {
        return this.distortion === 'AFFINE' || this.distortion === 'PERSPECTIVE';
      },

      minControlPoints() {
        switch (this.distortion) {
          case 'AFFINE':
            return 1;
          case 'PERSPECTIVE':
            return 4;
          default:
            return 0;
        }
      },

      distortionArgumentsName() {
        switch (this.distortion) {
          case 'AFFINE':
          case 'PERSPECTIVE':
            return 'Control Points';

          case 'AFFINE_PROJECTION':
            return 'Affine Matrix';

          case 'PERSPECTIVE_PROJECTION':
            return 'Perspective Matrix';

          case 'ARC':
            return 'Arc Parameters';
        }
      }
    },

    watch: {
      args: {
        handler(args) {
          if (!arraysEqual(args, this.localArgs)) {
            this.localArgs = args.slice();
          }
        },

        immediate: true
      },

      localArgs(args) {
        if (!arraysEqual(args, this.args)) {
          this.$emit('update:args', args);
        }
      }
    },

    methods: {
      onPickSourceApex({location, offset}) {
        if (this.sourceImage) {
          let x, y;

          switch (location) {
            case 'tl':
              x = 0;
              y = 0;
              break;

            case 'tr':
              x = this.sourceImage.width - 1;
              y = 0;
              break;

            case 'br':
              x = this.sourceImage.width - 1;
              y = this.sourceImage.height - 1;
              break;

            case 'bl':
              x = 0;
              y = this.sourceImage.height - 1;
              break;
          }

          x += this.viewportOffset.x;
          y += this.viewportOffset.y;

          this.localArgs.splice(offset, 2, x, y);
        }
      }
    }
  }
</script>