<template>
    <div>
        <VSelect
                v-model="proxyFilterPreset"
                :items="presetNames"
                label="Filter Preset"
                dense
                filled
                :disabled="disabled"
        />

        <VExpandTransition>
            <FilterConfigurator
                    v-if="filterPreset === 'CUSTOM'"
                    :filter-func.sync="filterFunc"
                    :filter-func-cubic-b-c_b.sync="filterFuncCubicBC_b"
                    :filter-func-cubic-b-c_c.sync="filterFuncCubicBC_c"
                    :window-func.sync="windowFunc"
                    :window-func-cubic-b-c_b.sync="windowFuncCubicBC_b"
                    :window-func-cubic-b-c_c.sync="windowFuncCubicBC_c"
                    :support.sync="support"
                    :scale.sync="scale"
                    :blur.sync="blur"
                    :window-support.sync="windowSupport"
                    :disabled="disabled"
            />
        </VExpandTransition>
    </div>
</template>

<script>
  import {simpleProxy} from '../../../../utils';
  import lens from '@alxcube/lens';
  import FilterConfigurator from './filter-configurator';
  const {filterPresets} = lens;
  
  function createFilterOptionProxy(name) {
    return {
      get() {
        return this.localFilterOptions[name];
      },
      
      set(value) {
        this.localFilterOptions[name] = value;
        this.$emit('update:custom-filter-options', this.localFilterOptions);
      }
    };
  }

  const props = {
    filterPreset: String,

    customFilterOptions: Object,

    disabled: Boolean
  };

  export default {
    name: 'FilterSettings',
    
    components: {
      FilterConfigurator
    },

    props,

    data() {
      return {
        localFilterOptions: {}
      };
    },

    computed: {
      proxyFilterPreset: simpleProxy('filterPreset'),
      
      presetNames() {
        let names = Object.keys(filterPresets);
        names.push('CUSTOM');
        return names;
      },
      
      filterFunc: createFilterOptionProxy('filterFunc'),
      filterFuncCubicBC_b: createFilterOptionProxy('filterFuncCubicBC_b'),
      filterFuncCubicBC_c: createFilterOptionProxy('filterFuncCubicBC_c'),
      windowFunc: createFilterOptionProxy('windowFunc'),
      windowFuncCubicBC_b: createFilterOptionProxy('windowFuncCubicBC_b'),
      windowFuncCubicBC_c: createFilterOptionProxy('windowFuncCubicBC_c'),
      support: createFilterOptionProxy('support'),
      scale: createFilterOptionProxy('scale'),
      blur: createFilterOptionProxy('blur'),
      windowSupport: createFilterOptionProxy('windowSupport')
    },

    methods: {
      emitFilterOptionsUpdate() {
        this.$emit('update:custom-filter-options', this.localFilterOptions);
      }
    },

    watch: {
      customFilterOptions: {
        handler(newOptions, oldOptions) {
          if (newOptions !== oldOptions) {
            Object.keys(newOptions).forEach(key => this.$set(this.localFilterOptions, key, newOptions[key]));
          }
        },

        immediate: true
      }
    }
  }
</script>