import {isEqual, cloneDeep, kebabCase} from 'lodash';

export function createPropWatcher(dataPropName) {
  return function (value) {
    if (!isEqual(value, this[dataPropName])) {
      this[dataPropName] = cloneDeep(value);
    }
  };
}

export function createDataWatcher(propName) {
  return function (value) {
    if (!isEqual(value, this[propName])) {
      this.$emit('update:' + kebabCase(propName), cloneDeep(value));
    }
  }
}

export function createPropDataWatchers(pairs) {
  const watchers = {};

  Object.keys(pairs).forEach(propName => {
    let dataName = pairs[propName];

    watchers[propName] = {
      handler: createPropWatcher(dataName),
      immediate: true,
      deep: true
    };

    watchers[dataName] = {
      handler: createDataWatcher(propName),
      deep: true
    };
  });

  return watchers;
}

export function arraysEqual(arr1, arr2) {
  if (arr1.length !== arr2.length) {
    return false;
  }

  for (let i = 0; i < arr1.length; i++) {
    if (arr1[i] !== arr2[i]) {
      return false;
    }
  }

  return true;
}

export function simpleProxy(propName) {
  const propKebeb = propName.replace(/([a-z0-9]|(?=[A-Z]))([A-Z])/g, '$1-$2').toLowerCase();

  return {
    get() {
      return this[propName];
    },

    set(value) {
      this.$emit(`update:${propKebeb}`, value);
    }
  };
}

export function massSimpleProxy(names, prefix = 'proxy', exclude = []) {
  return names.reduce((result, name) => {
    if (exclude.indexOf(name) < 0) {
      const proxyName = prefix + name.charAt(0).toUpperCase() + name.substring(1);
      result[proxyName] = simpleProxy(name);
    }

    return result;
  }, {});
}

export function preloadImage(src) {
  return new Promise((resolve, reject) => {
    const preloader = new Image();

    preloader.onload = () => resolve(preloader);

    preloader.onerror = () => reject(new Error("Couldn't preload image"));

    preloader.src = src;
  });
}

export function readImageDataUrl(imageFile, onprogress) {
  return new Promise((resolve, reject) => {
    const reader        = new FileReader();

    reader.onloadend    = () => resolve(reader.result);

    reader.onerror      = () => reject(reader.error);

    if (onprogress) {
      reader.onprogress   = event => {
        let bytesRead   = event.loaded,
            fileSize    = imageFile.size,
            progress    = bytesRead / fileSize;

        onprogress(progress, bytesRead, fileSize);
      }
    }

    reader.readAsDataURL(imageFile);
  });
}