<template>
    <div>
        <VSpeedDial
                v-if="mobile"
                v-model="fab"
                direction="top"
                absolute
                bottom
                right
        >
            <template v-slot:activator>
                <VBtn
                        fab
                        color="teal"
                        :loading="loading"
                        :disabled="disabled"

                >
                    <VBadge :value="showMainBadge && !fab" dot color="red" overlap>
                        <VIcon v-if="fab">mdi-close</VIcon>
                        <VIcon v-else>mdi-dots-vertical-circle</VIcon>
                    </VBadge>
                </VBtn>
            </template>

            <VBtn
                    fab
                    @click="$emit('distort')"
                    color="primary"
                    :disabled="disabled || disableDistortion"
            >
                <VIcon>mdi-camera-iris</VIcon>
            </VBtn>

            <VBtn
                    fab
                    @click="$emit('show-input-settings')"
                    color="red"
                    :disabled="disabled"
            >
                <VIcon>mdi-cogs</VIcon>
            </VBtn>

            <VBtn
                    v-if="showOutputInfo"
                    fab
                    @click="onShowOutputInfo"
                    color="blue"
                    :disabled="disabled"
            >
                <VBadge
                        :value="showOutputInfoBadge"
                        dot
                        color="red"
                        overlap
                >
                    <VIcon>mdi-information-outline</VIcon>
                </VBadge>
            </VBtn>
        </VSpeedDial>

        <VBtn
                v-else
                fab
                @click.stop="$emit('distort')"
                color="primary"
                :loading="loading"
                :disabled="disabled || disableDistortion"
                absolute
                bottom
                right
        >
            <VIcon>mdi-camera-iris</VIcon>
        </VBtn>
    </div>
</template>

<script>
  export default {
    name: 'DistortionButton',

    props: {
      mobile: Boolean,
      disabled: Boolean,
      loading: Boolean,
      disableDistortion: Boolean,
      showOutputInfo: Boolean
    },

    data() {
      return {
        fab: false,
        showMainBadge: false,
        showOutputInfoBadge: false
      }
    },

    watch: {
      showOutputInfo(val) {
        this.showMainBadge = val;
        this.showOutputInfoBadge = val;
      }
    },

    methods: {
      onShowOutputInfo() {
        this.showMainBadge = false;
        this.showOutputInfoBadge = false;
        this.$emit('show-output-info');
      }
    }
  }
</script>