<template>
    <div>
        <h4>Output Viewport Settings</h4>

        <VRadioGroup
                label="Output Viewport"
                v-model="selectedViewport"
        >
            <VRadio
                    label="Source Image Viewport"
                    value="source"
                    :disabled="disabled || sourceDisabled"
            />

            <VRadio
                    label="Best Fit"
                    value="bestfit"
                    :disabled="disabled || bestFitDisabled"
            />

            <VRadio
                    label="Custom"
                    value="custom"
                    :disabled="disabled"
            />
        </VRadioGroup>

        <VExpandTransition>
            <VRow dense v-if="selectedViewport === 'custom'">
                <VCol cols="3">
                    <VTextField
                            v-model.number="x1"
                            label="x1"
                            dense
                            outlined
                            type="number"
                            :disabled="disabled"
                    />
                </VCol>

                <VCol cols="3">
                    <VTextField
                            v-model.number="y1"
                            label="y1"
                            dense
                            outlined
                            type="number"
                            :disabled="disabled"
                    />
                </VCol>

                <VCol cols="3">
                    <VTextField
                            v-model.number="x2"
                            label="x2"
                            dense
                            outlined
                            type="number"
                            :disabled="disabled"
                    />
                </VCol>

                <VCol cols="3">
                    <VTextField
                            v-model.number="y2"
                            label="y2"
                            dense
                            outlined
                            type="number"
                            :disabled="disabled"
                    />
                </VCol>
            </VRow>
        </VExpandTransition>
    </div>
</template>

<script>
  function makeComputedVpProp(name) {
    return {
      get() {
        return this.localViewport[name];
      },

      set(val) {
        this.localViewport[name] = val;
        this.emitViewport();
      }
    };
  }

  export default {
    name: 'ViewportSettings',

    props: {
      bestFit: Boolean,

      useCustomViewport: Boolean,

      customViewport: Object,

      bestFitDisabled: Boolean,

      sourceDisabled: Boolean,

      disabled: Boolean
    },

    data() {
      return {
        localViewport: {
          x1: 0,
          y1: 0,
          x2: 0,
          y2: 0
        }
      };
    },

    computed: {
      selectedViewport: {
        get() {
          if (this.useCustomViewport) {
            return 'custom';
          } else if (this.bestFit) {
            return 'bestfit';
          } else {
            return 'source';
          }
        },

        set(value) {
          switch (value) {
            case 'source':
              this.$emit('update:best-fit', false);
              this.$emit('update:use-custom-viewport', false);
              break;

            case 'bestfit':
              this.$emit('update:best-fit', true);
              this.$emit('update:use-custom-viewport', false);
              break;

            case 'custom':
              this.$emit('update:best-fit', false);
              this.$emit('update:use-custom-viewport', true);
              this.emitViewport();
              break;
          }
        },
      },

      x1: makeComputedVpProp('x1'),
      y1: makeComputedVpProp('y1'),
      x2: makeComputedVpProp('x2'),
      y2: makeComputedVpProp('y2')
    },

    methods: {
      emitViewport() {
        this.$emit('update:custom-viewport', this.localViewport);
      }
    },

    watch: {
      customViewport: {
        handler(newViewport, oldViewport) {
          if (newViewport !== oldViewport) {
            let {x1, y1, x2, y2} = newViewport;
            this.localViewport = {x1, y1, x2, y2};
          }
        },

        immediate: true
      }
    }
  }
</script>

<style scoped>

</style>