<template>
    <div>
        <VTextField
                label="Processing Time"
                :value="distortionTimeString"
                readonly
                dense
                outlined
        />

        <h5 class="mt-3">Output Viewport</h5>

        <VRow dense>
            <VCol
                    v-for="(value, key) in distortedViewport"
                    :key="key"
                    :cols="key === 'width' || key === 'height' ? 6 : 3"
            >
                <VTextField
                        :label="key"
                        :value="value"
                        readonly
                        dense
                        outlined
                />
            </VCol>
        </VRow>

        <VBtn @click="$emit('use-distorted-as-source')">
            Use As Source Image
        </VBtn>
    </div>
</template>

<script>
  export default {
    name: 'OutputInfo',

    props: {
      processingTime: [Number, null],
      distortedViewport: Object
    },

    computed: {
      distortionTimeString() {
        if (this.processingTime === null) {
          return '-';
        }

        let msec = this.processingTime;
        let hours = Math.floor(msec / 3600000);
        msec -= hours * 3600000;
        let minutes = Math.floor(msec / 60000);
        msec -= minutes * 60000;
        let seconds = Math.floor(msec / 1000);
        msec -= seconds * 1000;
        let time = [];

        if (hours) {
          time.push(hours + ' h');
        }

        if (hours || minutes) {
          time.push((minutes < 10 ? '0' : '') + minutes + ' min');
        }

        let msecStr = (msec < 10 ? '00' : (msec < 100 ? '0' : '')) + msec;

        time.push(((hours || minutes) && seconds < 10 ? '0' : '') + seconds + '.' + msecStr + ' sec');

        return time.join(',');
      }
    }
  }
</script>