import buildsDistortionOptions from './builds-distortion-options';
import buildsDistortion from './builds-distotion';
import buildsImageObject from './builds-image-object';

import lens from '@alxcube/lens';

export default {
  mixins: [
    buildsDistortionOptions,
    buildsDistortion,
    buildsImageObject
  ],

  data() {
    return {
      distortedImage: null,

      performingDistortion: false,

      distortionTime: null
    };
  },

  methods: {
    performDistortion(img) {
      const image = this.buildImageObject(img);

      try {
        var distortion = this.buildDistortion(image);
      } catch (err) {
        return Promise.reject(err);
      }

      return lens.distort(
        image,
        distortion,
        this.buildDistortionOptions()
      );
    },

    distort(img) {
      this.clearDistortionData();

      this.performingDistortion = true;
      const startTime = new Date().getTime();

      return this.performDistortion(img)
        .then(distortedImage => {
          this.distortionTime = new Date().getTime() - startTime;
          this.distortedImage = distortedImage;
        })
        .finally(() => this.performingDistortion = false);
    },

    clearDistortionData() {
      this.distortedImage = null;
      this.distortionTime = null;
    }
  }
}