<template>
    <VCard class="image-area d-flex">
        <VToolbar class="flex-grow-0" dense>
            <VTabs color="white" v-model="imageTab">
                <VTab>
                    Original
                </VTab>

                <VTab
                        :disabled="isTabDistortedDisabled"
                >
                    Distorted
                </VTab>
            </VTabs>

            <VSpacer/>

            <VMenu :close-on-content-click="false" v-model="tuneMenu">
                <template v-slot:activator="{on}">
                    <VBtn v-on="on" icon>
                        <VIcon>mdi-tune</VIcon>
                    </VBtn>
                </template>

                <VCard>
                    <VCardText>
                        <VSwitch
                                label="Apply Viewport Offset"
                                v-model="applyViewportOffset"
                                dense
                        />

                        <VSwitch
                                label="Show Axes"
                                v-model="showAxes"
                                dense
                        />
                    </VCardText>

                    <VCardActions>
                        <VBtn @click="tuneMenu = false">Close</VBtn>
                    </VCardActions>
                </VCard>
            </VMenu>
        </VToolbar>

        <VSheet class="overflow-hidden flex-grow-1 flex-shrink-1 image-area__viewport" tile>
            <template v-if="showAxes">
                <div class="image-area__axis x-axis" :style="xAxisPosition"></div>
                <div class="image-area__axis y-axis" :style="yAxisPosition"></div>
            </template>

            <ImageViewer
                    ref="imageViewer"
                    :src="src"
                    :scale.sync="scale"
                    :position.sync="position"
                    :min-scale="minScale"
                    :max-scale="maxScale"
                    class="image-area__image-plane fill-height"
                    :offset="imageOffset"
                    @click="$emit('plane-click', $event)"
                    @mousemove.native="onViewportMouseMove"
                    @mouseleave.native="onViewportMouseLeave"
                    :class="imageViewerClass"
            />
        </VSheet>

        <VCardActions>
            <VToolbar dense flat>
                <VBtn icon small @click="zoom(false)" :disabled="isMinScale">
                    <VIcon>mdi-magnify-minus-outline</VIcon>
                </VBtn>

                <VMenu top>
                    <template v-slot:activator="{on}">
                        <VBtn v-on="on" small width="30" class="ml-2">
                            {{ scalePercentage }}%
                        </VBtn>
                    </template>

                    <VList>
                        <VListItem @click="scale = 1">
                            <VListItemTitle>
                                100%
                            </VListItemTitle>
                        </VListItem>

                        <VListItem @click="fit">
                            <VListItemTitle>
                                Fit
                            </VListItemTitle>
                        </VListItem>
                    </VList>
                </VMenu>

                <VBtn icon small class="ml-2" @click="zoom(true)" :disabled="isMaxScale">
                    <VIcon>mdi-magnify-plus-outline</VIcon>
                </VBtn>

                <VSpacer/>


                <template v-if="crosshair">
                    <VSheet class="hidden-sm-and-down pa-1 text-center" width="100">
                        x: {{ cursorAtX }}
                    </VSheet>

                    <VSheet class="hidden-sm-and-down pa-1 text-center ml-2" width="100">
                        y: {{ cursorAtY }}
                    </VSheet>
                </template>
            </VToolbar>
        </VCardActions>
    </VCard>
</template>

<script>
  import ImageViewer from './image-viewer';

  export default {
    name: 'ImageArea',

    components: {
      ImageViewer
    },

    props: {
      sourceImage: Image,
      distortedImage: Object,
      initialViewportOffset: Object,
      crosshair: Boolean
    },

    data() {
      return {
        imageTab: 0,

        tuneMenu: false,

        applyViewportOffset: false,

        showAxes: true,

        scale: 1,

        position: {
          x: 0,
          y: 0
        },

        minScale: 0.1,

        maxScale: 4,

        cursorAt: {
          x: null,
          y: null
        }
      };
    },

    computed: {
      image() {
        return this.imageTab === 1 && this.distortedImage && this.distortedImage.image || this.sourceImage;
      },

      src() {
        return this.image.src;
      },

      isTabDistortedDisabled() {
        return !this.distortedImage;
      },

      scalePercentage() {
        return Math.round(this.scale * 100);
      },

      isMinScale() {
        return this.scale === this.minScale;
      },

      isMaxScale() {
        return this.scale === this.maxScale;
      },

      imageOffset() {
        if (this.applyViewportOffset) {
          if (this.imageTab === 1 && this.distortedImage) {
            const {x1: x, y1: y} = this.distortedImage.viewport;
            return {x, y};
          } else if (this.initialViewportOffset) {
            const {x, y} = this.initialViewportOffset;
            return {x, y}
          }
        }

        return null;
      },
      
      imageViewerClass() {
        return {
          crosshair: !!this.crosshair
        };
      },

      xAxisPosition() {
        return {
          left: this.position.x + 'px'
        }
      },

      yAxisPosition() {
        return {
          top: this.position.y + 'px'
        }
      },

      cursorAtX() {
        const x = this.cursorAt.x;
        return x !== null ? Math.round(x) : '-';
      },

      cursorAtY() {
        const y = this.cursorAt.y;
        return y !== null ? Math.round(y) : '-';
      },

      cursorAtText() {
        let {x, y} = this.cursorAt;

        if (x === null) {
          x = '-';
        }

        if (y === null) {
          y = '-';
        }

        return `x: ${x}; y: ${y}`;
      }
    },

    watch: {
      distortedImage(newVal) {
        if (!newVal) {
          this.imageTab = 0;
        } else {
          this.imageTab = 1;
        }
      }
    },

    mounted() {
      this.fit();
    },

    methods: {
      zoom(zoomIn) {
        let newScale = this.scale * (zoomIn ? 1.25 : 0.75);

        if (newScale < this.minScale) {
          newScale = this.minScale;
        }

        if (newScale > this.maxScale) {
          newScale = this.maxScale;
        }

        this.scale = newScale;
      },

      fit() {
        const {width: vpWidth, height: vpHeight} = this.$refs.imageViewer.$el.getBoundingClientRect();
        let {width, height} = this.image;
        const ratio = width / height;

        if (width > vpWidth) {
          width = vpWidth;
          height = width / ratio;
        }

        if (height > vpHeight) {
          height = vpHeight;
          width = height * ratio;
        }

        let x, y;

        x = vpWidth / 2 - width / 2;
        y = vpHeight / 2 - height / 2;

        let imageOffset = this.imageOffset;

        if (imageOffset) {
          x -= imageOffset.x;
          y -= imageOffset.y;
        }

        this.scale = width / this.image.width;

        this.$nextTick(() => {
          this.position = {x, y};
        });
      },

      onViewportMouseMove(event) {
        const {top, left} = this.$refs.imageViewer.$el.getBoundingClientRect();
        const containerX = event.pageX - left;
        const containerY = event.pageY - top;
        const scale = 1 / this.scale;
        const x = (containerX - this.position.x) * scale;
        const y = (containerY - this.position.y) * scale;
        this.cursorAt.x = x;
        this.cursorAt.y = y;
      },

      onViewportMouseLeave(event) {
        this.cursorAt = {x: null, y: null};
      }
    }
  }
</script>

<style scoped lang="scss">
    .image-area {
        flex-basis: 100%;
        flex-direction: column;
    }
    
    .image-area__image-plane {
        &.crosshair {
            cursor: crosshair;
        }
    }

    .image-area__viewport {
        position: relative;

        .image-area__axis {
            position: absolute;
            background-color: aquamarine;

            &.x-axis {
                top: 0;
                height: 100%;
                width: 1px;
                margin-left: -0.5px;
            }

            &.y-axis {
                left: 0;
                width: 100%;
                height: 1px;
                margin-top: -0.5px;
            }
        }
    }
</style>