<template>
    <div>
        <VSelect
                :label="label"
                :items="filterFunctionNames"
                v-model="proxyName"
                filled
                dense
                :disabled="disabled"
        />

        <VExpandTransition>
            <div v-if="name === 'cubicBC'">
                <VTextField
                        label="b"
                        v-model.number="proxyCubicBC_b"
                        type="number"
                        dense
                        outlined
                        :disabled="disabled"
                />

                <VTextField
                        label="c"
                        v-model.number="proxyCubicBC_c"
                        type="number"
                        dense
                        outlined
                        :disabled="disabled"
                />
            </div>
        </VExpandTransition>
    </div>
</template>

<script>
  import {massSimpleProxy} from '../../../../utils';

  import lens from '@alxcube/lens';

  const filter = lens.filter;

  const props = {
    name: String,

    cubicBC_b: Number,

    cubicBC_c: Number,

    label: String,

    disabled: Boolean
  };

  export default {
    name: 'FilterFunctionConfigurator',

    props,

    computed: {
      ...massSimpleProxy(Object.keys(props), 'proxy', ['label', 'disabled']),

      filterFunctionNames() {
        return Object.keys(filter.filterFunctions);
      }
    }
  }
</script>