import {arraysEqual} from '../../../../utils';

export default {
  props: {
    value: Array,
    disabled: Boolean
  },

  data() {
    return {
      localValue: []
    };
  },

  methods: {
    setValue(index, value) {
      if (typeof value === 'string') {
        value = parseFloat(value.replace(',', '.'));
      }

      this.$set(this.localValue, index, value);
    },

    getFilteredArray() {
      return this.localValue.map(item => {
        if (isNaN(item) || item === undefined) {
          return null;
        }

        return item;
      });
    },

    emitInput() {
      this.$emit('input', this.getFilteredArray());
    }
  },

  watch: {
    value: {
      handler(newValue) {
        if (!arraysEqual(newValue, this.localValue)) {
          this.localValue = newValue.slice();
        }
      },

      immediate: true
    },

    localValue(newValue) {
      if (!arraysEqual(newValue, this.value)) {
        this.emitInput();
      }
    }
  }
};