<template>
    <div>
        <VRow dense>
            <VCol cols="4"
                  v-for="({label, index}, i) in argsMap"
                  :key="label"
            >
                <VTextField
                        type="number"
                        :label="label"
                        dense
                        outlined
                        :value="localValue[index]"
                        @change="setValue(index, $event)"
                        :disabled="disabled"
                />
            </VCol>
        </VRow>
    </div>
</template>

<script>
  import distortionArgsMixin from './distortion-args-mixin';

  // Notice indexes: arguments order is [sx, ry, rx, sy, tx, ty]
  const argsMap = [
    {
      label: 'sx',
      index: 0
    },
    {
      label: 'rx',
      index: 2
    },
    {
      label: 'tx',
      index: 4
    },
    {
      label: 'ry',
      index: 1
    },
    {
      label: 'sy',
      index: 3
    },
    {
      label: 'ty',
      index: 5
    }
  ];

  export default {
    name: 'AffineProjectionArgs',

    mixins: [distortionArgsMixin],

    data() {
      return {
        argsMap
      };
    }
  }
</script>