<template>
    <VContainer fluid fill-height align-stretch>
        <FileSelectionArea
                v-if="!sourceImage && !showLoading"
                @input="onFileSelected"
                class="fill-height d-flex"
                style="width: 100%;"
        >
            <div class="d-flex align-content-center fill-height justify-center align-center">
                <p class="text-center">
                    <VIcon size="100">mdi-image</VIcon>
                    <br>
                    <template v-if="!isMobile">
                        Drag image here or click to select
                    </template>
                    <template v-else>
                        Tap to select image
                    </template>
                </p>
            </div>
        </FileSelectionArea>

        <VRow v-else dense class="fill-height">
            <VCol cols="12" md="8" lg="9">

                <ImageArea
                        class="fill-height"
                        v-if="sourceImage"
                        :source-image="sourceImage"
                        :distorted-image="distortedImage"
                        :initial-viewport-offset="imageOptions.initialViewportOffset"
                        @plane-click="onImagePlaneClick"
                        :crosshair="waitingForPoint !== null"
                />
            </VCol>

            <VCol cols="12" md="4" lg="3" class="d-none d-md-flex">
                <div class="relative-wrapper">
                    <div class="absolute-wrapper">
                        <VTabs v-model="inputOutputTab">
                            <VTab>Input Settings</VTab>
                            <VTab :disabled="isOutputTabDisabled">Output Info</VTab>

                            <VTabItem>
                                <VCard>
                                    <VCardText>
                                        <portal to="mobileInputSettings" :disabled="!isMobile">
                                            <InputSettings
                                                    :source-image="sourceImage"
                                                    :image-options.sync="imageOptions"
                                                    :distortion.sync="distortion"
                                                    :distortion-options.sync="options"
                                                    :args-active-offset="waitingForPoint"
                                                    :disabled="showLoading"
                                                    @change-image="onFileSelected"
                                                    @pick-point="onPickPoint"
                                                    @pick-point-cancel="onPickPointCancel"
                                            />
                                        </portal>
                                    </VCardText>
                                </VCard>
                            </VTabItem>

                            <VTabItem>
                                <VCard v-if="hasDistortionResult">
                                    <VCardText>
                                        <portal to="mobileOutputInfo" :disabled="!isMobile">
                                            <OutputInfo
                                                    class="mt-2"
                                                    :processing-time="distortionTime"
                                                    :distorted-viewport="distortedViewport"
                                                    @use-distorted-as-source="onUseDistortedAsSource"
                                            />
                                        </portal>
                                    </VCardText>
                                </VCard>
                            </VTabItem>
                        </VTabs>
                    </div>
                </div>
            </VCol>
        </VRow>

        <VDialog
                v-if="isMobile"
                fullscreen
                v-model="showMobileInputSettings"
                transition="dialog-bottom-transition"
                scrollable
        >
            <VCard>
                <VToolbar>
                    <VBtn icon @click.stop="closeInputSettingsDialog">
                        <VIcon>mdi-close</VIcon>
                    </VBtn>

                    <VToolbarTitle>Input Settings</VToolbarTitle>
                </VToolbar>

                <VCardText>
                    <PortalTarget name="mobileInputSettings"/>
                </VCardText>

                <VCardActions>
                    <VBtn color="primary" @click.stop="onDistortBtnClick">
                        <VIcon>mdi-camera-iris</VIcon> Distort
                    </VBtn>
                </VCardActions>
            </VCard>
        </VDialog>

        <VDialog
                v-if="isMobile"
                fullscreen
                v-model="showMobileOutputInfo"
                transition="dialog-bottom-transition"
        >
            <VCard>
                <VToolbar>
                    <VBtn icon @click.stop="closeOutputInfoDialog">
                        <VIcon>mdi-close</VIcon>
                    </VBtn>

                    <VToolbarTitle>Output Info</VToolbarTitle>
                </VToolbar>

                <VCardText>
                    <PortalTarget name="mobileOutputInfo"/>
                </VCardText>
            </VCard>
        </VDialog>

        <DistortionButton
                v-show="isSourceImageLoaded"
                :mobile="isMobile"
                :loading="performingDistortion"
                @distort="onDistortBtnClick"
                @show-input-settings="openInputSettingsDialog"
                @show-output-info="openOutputInfoDialog"
                :show-output-info="hasDistortionResult"
        />

        <VOverlay v-if="showLoading">
            <VProgressCircular
                    indeterminate
                    size="100"
            />
        </VOverlay>
    </VContainer>
</template>

<script>
    import FileSelectionArea from '../../file-selection-area/index';
    import distortsImage from './mixins/distorts-image';
    import loadsSourceImage from './mixins/loads-source-image';
    import ImageArea from './image-area/image-area';
    import InputSettings from './input-settings';
    import OutputInfo from './output-info';
    import DistortionButton from './distortion-button';

    export default {
      name: "LensPlayground",

      mixins: [distortsImage, loadsSourceImage],

      components: {
        FileSelectionArea,
        ImageArea,
        InputSettings,
        OutputInfo,
        DistortionButton
      },

      data() {
        return {
          inputOutputTab:         0,
          showMobileInputSettings: false,
          showMobileOutputInfo: false,
          fab: false,
          // When set to number, write picked coords from this index to distortion args
          waitingForPoint: null
        };
      },

      computed: {
        isMobile() {
          const breakpoint = this.$vuetify.breakpoint.name;
          return breakpoint === 'sm' || breakpoint === 'xs';
        },

        isSourceImageLoaded() {
          return !!this.sourceImage;
        },

        hasDistortionResult() {
          return !!this.distortedImage;
        },

        isOutputTabDisabled() {
          return !this.hasDistortionResult;
        },

        showLoading() {
          return this.loadingImage || this.performingDistortion;
        },

        distortedViewport() {
          if (!this.distortedImage) {
            return null;
          }

          const {x1, y1, x2, y2} = this.distortedImage.viewport;
          const {width, height} = this.distortedImage;

          return {x1, y1, x2, y2, width, height};
        }
      },

      watch: {
        'distortion.name': function () {
          this.waitingForPoint = null;
        }
      },

      methods: {
        onFileSelected(files) {
          if (files) {
            if (this.isMobile) {
              this.closeInputSettingsDialog();
            }

            this.sourceImage = null;
            this.distortedImage = null;
            this.imageOptions.initialViewportOffset = {x: 0, y: 0};
            this.loadImageFile(files[0]);
          }
        },

        onUseDistortedAsSource() {
          this.closeInputSettingsDialog();
          this.closeOutputInfoDialog();

          this.sourceImage = this.distortedImage.image;
          this.imageOptions.initialViewportOffset = {
            x: this.distortedImage.viewport.x1,
            y: this.distortedImage.viewport.y1
          };
          this.distortedImage = null;
          this.inputOutputTab = 0;
        },

        onDistortBtnClick() {
          this.closeInputSettingsDialog();
          this.closeOutputInfoDialog();

          this.inputOutputTab = 0;

          return this.distort(this.sourceImage)
            .then(() => {
              this.inputOutputTab = 1;
            });
        },

        onPickPoint(offset) {
          if (this.isMobile) {
            this.closeInputSettingsDialog();
          }

          this.waitingForPoint = offset;
        },

        onPickPointCancel() {
          this.waitingForPoint = null;
        },

        onImagePlaneClick({x, y}) {
          if (this.waitingForPoint !== null) {
            this.setControlPoint(x, y, this.waitingForPoint);
            this.waitingForPoint = null;

            if (this.isMobile) {
              this.openInputSettingsDialog();
            }
          }
        },

        openInputSettingsDialog() {
          this.showMobileInputSettings = true;
        },

        closeInputSettingsDialog() {
          this.showMobileInputSettings = false;
        },

        openOutputInfoDialog() {
          this.showMobileOutputInfo = true;
        },

        closeOutputInfoDialog() {
          this.showMobileOutputInfo = false;
        }
      }
    }
</script>

<style scoped lang="scss">

    .relative-wrapper {
        position: relative;
        max-height: 100%;
        height: 100%;
        width: 100%;

        .absolute-wrapper {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            overflow-y: auto;
        }
    }
</style>