import {readImageDataUrl, preloadImage} from '../../../../utils';

export default {
  data() {
    return {
      sourceImage: null,

      loadingImage: false,

      loadingImageProgress: null
    };
  },

  methods: {
    loadImageFile(file) {
      this.sourceImage = null;
      this.loadingImage = true;
      this.loadingImageProgress = 0;

      readImageDataUrl(file, progress => this.loadingImageProgress = progress)
        .then(preloadImage)
        .then(img => this.sourceImage = img)
        .finally(() => {
          this.loadingImage = false;
          this.loadingImageProgress = null;
        });
    }
  }
};