<template>
    <div>
        <FileSelectionArea class="mb-3" @input="$emit('change-image', $event)" :disabled="disabled">
            <template v-slot:default="{click}">
                <VBtn :disabled="disabled" @click="click">Change Image</VBtn>
            </template>
        </FileSelectionArea>

        <VSelect
                v-model="selectedInterpolationMethod"
                :items="interpolationMethods"
                label="Interpolation Method"
                dense
                filled
                :disabled="disabled"
        />

        <VSelect
                v-model="selectedVirtualPixelMethod"
                :items="virtualPixelMethods"
                label="Virtual Pixel Method"
                dense
                filled
                :disabled="disabled"
        />

        <ColorInput
                label="Background Color"
                v-model="selectedBackgroundColor"
                :disabled="disabled"
        />

        <h5>Initial Viewport Offset</h5>

        <VRow dense>
            <VCol cols="6">
                <VTextField
                        type="number"
                        v-model.number="localViewportOffset.x"
                        label="x"
                        dense
                        outlined
                        :disabled="disabled"
                />
            </VCol>

            <VCol cols="6">
                <VTextField
                        type="number"
                        v-model.number="localViewportOffset.y"
                        label="y"
                        dense
                        outlined
                        :disabled="disabled"
                />
            </VCol>
        </VRow>
    </div>
</template>

<script>
  import ColorInput from './color-input';
  import FileSelectionArea from '../../file-selection-area/index';

  import { interpolation, vpx } from '@alxcube/lens';

  function makeItems(object) {
    return Object.keys(object).map(text => ({
      text,
      value: object[text]
    }));
  }

  export default {
    name: 'ImageSettings',

    components: {
      ColorInput,
      FileSelectionArea
    },

    props: {
      interpolationMethod: {
        type: Number,
        default: interpolation.AVERAGE
      },

      virtualPixelMethod: {
        type: Number,
        default: vpx.TRANSPARENT
      },

      backgroundColor: {
        type: Array,
        default() {
          return [0, 0, 0, 0];
        }
      },

      viewportOffset: {
        type: Object,
        default() {
          return {x: 0, y: 0}
        }
      },

      disabled: Boolean
    },

    data() {
      return {
        localViewportOffset: {x: 0, y: 0}
      };
    },

    computed: {
      selectedInterpolationMethod: {
        get() {
          return this.interpolationMethod;
        },

        set(method) {
          this.$emit('update:interpolation-method', method);
        }
      },

      selectedVirtualPixelMethod: {
        get() {
          return this.virtualPixelMethod;
        },

        set(method) {
          this.$emit('update:virtual-pixel-method', method);
        }
      },

      selectedBackgroundColor: {
        get() {
          return this.backgroundColor;
        },

        set(color) {
          return this.$emit('update:background-color', color);
        }
      },

      interpolationMethods() {
        return makeItems(interpolation);
      },

      virtualPixelMethods() {
        return makeItems(vpx);
      }
    },

    watch: {
      viewportOffset: {
        handler({x, y}) {
          if (x !== this.localViewportOffset.x || y !== this.localViewportOffset.y) {
            this.localViewportOffset = {x, y};
          }
        },

        immediate: true
      },

      localViewportOffset: {
        handler({x, y}) {
          if (x !== this.viewportOffset.x || y !== this.viewportOffset.y) {
            this.$emit('update:viewport-offset', {x, y});
          }
        },

        deep: true
      }
    }
  }
</script>