<template>
    <div>
        <h5 class="mb-2">Custom Filter Settings</h5>

        <FilterFunctionConfigurator
                :name.sync="proxyFilterFunc"
                :cubic-b-c_b.sync="proxyFilterFuncCubicBC_b"
                :cubic-b-c_c.sync="proxyFilterFuncCubicBC_c"
                label="Filter Function"
                :disabled="disabled"
        />

        <FilterFunctionConfigurator
                :name.sync="proxyWindowFunc"
                :cubic-b-c_b.sync="proxyWindowFuncCubicBC_b"
                :cubic-b-c_c.sync="proxyWindowFuncCubicBC_c"
                label="Windowing Function"
                :disabled="disabled"
        />

        <VRow dense>
            <VCol cols="6">
                <VTextField
                        v-model.number="proxySupport"
                        type="number"
                        label="Support"
                        dense
                        outlined
                        :disabled="disabled"
                />
            </VCol>

            <VCol cols="6">
                <VTextField
                        v-model.number="proxyScale"
                        type="number"
                        label="Scale"
                        dense
                        outlined
                        :disabled="disabled"
                />
            </VCol>

            <VCol cols="6">
                <VTextField
                        v-model.number="proxyBlur"
                        type="number"
                        label="Blur"
                        dense
                        outlined
                        :disabled="disabled"
                />
            </VCol>

            <VCol cols="6">
                <VTextField
                        v-model.number="proxyWindowSupport"
                        type="number"
                        label="Window Support"
                        dense
                        outlined
                        :disabled="disabled"
                />
            </VCol>
        </VRow>
    </div>
</template>

<script>
  import {massSimpleProxy} from '../../../../utils';
  import FilterFunctionConfigurator from './filter-function-configurator';

  const props = {
    filterFunc: String,

    filterFuncCubicBC_b: Number,

    filterFuncCubicBC_c: Number,

    windowFunc: String,

    windowFuncCubicBC_b: Number,

    windowFuncCubicBC_c: Number,

    support: Number,

    scale: Number,

    blur: Number,

    windowSupport: Number,

    disabled: Boolean
  };

  export default {
    name: 'FilterConfigurator',

    components: {
      FilterFunctionConfigurator
    },

    props,

    computed: {
      ...massSimpleProxy(Object.keys(props), 'proxy', ['disabled'])
    }
  }
</script>