<template>
    <div>
        <VRow dense>
            <VCol cols="4"
                  v-for="(label, index) in coeffNames"
                  :key="label"
            >
                <VTextField
                        type="number"
                        :label="label"
                        dense
                        outlined
                        :value="localValue[index]"
                        @change="setValue(index, $event)"
                        :disabled="disabled"
                />
            </VCol>
        </VRow>
    </div>
</template>

<script>
  import distortionArgsMixin from './distortion-args-mixin';

  export default {
    name: 'PerspectiveProjectionArgs',

    mixins: [distortionArgsMixin],

    data() {
      return {
        coeffNames: ['sx', 'ry', 'tx', 'rx', 'sy', 'ty', 'px', 'py'],
      };
    }
  }
</script>